.formContainer {
    width: 100%;
    height: auto;
    background-color: #171719;
    border-top-right-radius: 30px;
    border-top-left-radius: 30px;
    padding: 20px;
}

.formBox {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.formInput {
    border-radius: 30px;
    padding: 15px 30px;
    width: 100%;
    height: 50px;
    border: 1px solid var(--background-color);
    background: var(--background-color);

    font-family: 'SuisseIntl-Regular', sans-serif;
    font-weight: 400;
    font-size: 16px;
    letter-spacing: -0.02em;
    color: rgba(250, 250, 250, 0.4);
    outline: none;
}

.formInput:hover,
.formInput:focus {
    border: 1px solid var(--button-color);
}

.formCalendar {
    border: 1px solid rgba(250, 250, 250, 0.2);
    border-radius: 30px;
    padding: 15px 30px;
    width: 100%;
    height: 50px;
    background: #171719;
    color: rgba(250, 250, 250, 0.4);

    font-family: 'SuisseIntl-Regular', sans-serif;
    font-weight: 400;
    font-size: 16px;
    letter-spacing: -0.02em;
    outline: none;
    transition: 200ms cubic-bezier(0.075, 0.82, 0.165, 1);

    &:hover,
    &:focus {
        border-color: var(--button-color);
    }
}

.datePicker {
    width: 100%;
}

.datepickerContainer {
    position: relative;
    display: grid;
}

.react-datepicker-wrapper {
    width: 100%;
}

.filterContainer {
    position: relative;
}

.iconSearch {
    stroke: var(--button-color);
    fill: none;
    position: absolute;
    right: 10%;
    top: 25%;
    transform: translate(-40%, 10%);
}

.iconDate {
    stroke: var(--button-color);
    fill: none;
    position: absolute;
    right: 10%;
    top: 25%;
    transform: translate(-40%, 10%);
}

@media only screen and (min-width: 768px) {
    .formContainer {
        height: auto;
        flex-direction: row;
        padding: 32px 32px 16px 32px;
    }

    .formBox {
        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: wrap;
        gap: 20px;
    }

    .formInput {
        border-radius: 30px;
        padding: 15px 30px;
        width: 254px;
        height: 50px;
        background: #0c0d0d;

        font-family: 'SuisseIntl-Regular', sans-serif;
        font-weight: 400;
        font-size: 16px;
        letter-spacing: -0.02em;
        color: rgba(250, 250, 250, 0.4);
        outline: none;
    }

    .formCalendar {
        width: 183px;
    }

    .iconDate {
        right: 10%;
    }
}

@media only screen and (min-width: 1440px) {
    .formContainer {
        height: 90px;
        padding: 20px 40px 16px 40px;
    }

    .formBox {
        display: flex;
        gap: 20px;
    }

    .iconDate {
        right: 10%;
    }
}